@font-face {
  font-family: "PT Sans";
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  src: url(./fonts/PTSans-Regular.woff2) format("woff2");
}
@font-face {
  font-family: "PT Sans";
  font-display: swap;
  font-weight: 700;
  font-style: normal;
  src: url(./fonts/PTSans-Bold.woff2) format("woff2");
}
.page {
  font-family: "PT Sans", sans-serif;
  color: #000;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  min-inline-size: 1920px;
  margin: 0 auto;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 320px;
}

.header__nav-list {
  display: flex;
  gap: 30px;
  list-style: none;
}

.header__link {
  text-decoration: none;
  color: #000;
}

.header__link:focus {
  outline: none;
}

.header__link:focus-visible {
  outline: 2px solid #808080;
  border-radius: 4px;
}

.header__link:hover {
  text-decoration: underline;
}

.header__button-search {
  max-inline-size: 25px;
  max-block-size: 25px;
  border: none;
  background-color: transparent;
  padding: 0;
  margin: 0;
}

.header__button-search:focus {
  outline: none;
}

.header__button-search:focus-visible {
  outline: 2px solid #808080;
  border-radius: 4px;
}

.custom-select {
  position: relative;
}

.header__select {
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
  background-color: transparent;
}

.custom-select::after {
  content: "";
  position: absolute;
  display: block;
  inset: 5px 28px;
  width: 10px;
  height: 10px;
  background-color: white;
}

.custom-select::after {
  content: "";
  position: absolute;
  display: block;
  inset: 5px 28px;
  width: 10px;
  height: 10px;
  background-image: url(./images/arrow.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  pointer-events: none;
}

.header__select:focus {
  outline: none;
}

.header__select:focus-visible {
  outline: 2px solid #808080;
  border-radius: 4px;
  border-color: transparent;
}

.header__search-form {
  display: flex;
  align-items: center;
  gap: 20px;
}

.header__input-search {
  min-block-size: 25px;
  border: 2px solid transparent;
  border-block-end: 1px solid rgba(51, 51, 51, 0.5);
}

.header__input-search:focus {
  outline: none;
}

.keyboard-focus {
  border: 2px solid #808080;
  border-radius: 4px;
}

.header__logo {
  line-height: 0px;
}

.header__logo:focus {
  outline: none;
}

.header__logo:focus-visible {
  outline: 2px solid #808080;
  border-radius: 4px;
}

.content__first-screen {
  display: flex;
  gap: 134px;
  background-image: url("./images/background.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding-inline: 312px;
}

.content__first-screen-title {
  margin-block-end: 40px;
  font-size: 50px;
  font-style: normal;
  font-weight: 700;
  line-height: 140%;
  color: #fff;
}

.first-screen__text {
  margin-block-end: 60px;
  line-height: 140%;
  color: #fff;
}

.first-screen__description-box {
  display: flex;
  width: 526px;
  flex-direction: column;
  align-items: flex-start;
  padding-block-start: 180px;
  padding-block-end: 234px;
}

.first-screen__link-about {
  display: flex;
  align-items: center;
  gap: 15px;
  background-color: transparent;
  text-decoration: none;
  color: #fff;
}

.first-screen__cards-list {
  display: flex;
  gap: 24px;
  flex-wrap: wrap;
  padding-block: 113px;
  list-style-type: none;
}

.first-screen__cards-box {
  display: flex;
  gap: 24px;
}

.first-screen__card {
  display: flex;
  flex-direction: column;
  gap: 118px;
  padding-block: 30px;
  padding-inline: 35px;
  border-radius: 20px;
  background-color: #5DB7DE;
}

.first-screen__item {
  flex-grow: 0;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li {
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
}

.visually-hidden {
  position: absolute;
  inline-size: 1px;
  block-size: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  white-space: nowrap;
}